import React, { useState, useEffect, useContext } from 'react';
import { GlobalContext } from '../../contexts/GlobalState';

function TimeDisplay() {
  const { today } = useContext(GlobalContext);

  // console.log(today.minute);

  let [currentTime, setCurrentTime] = useState({
    hour: today.hour,
    minute: today.minute,
    day: new Date().toLocaleString('default', { weekday: 'short' }),
    month: new Date().toLocaleString('default', { month: 'short' }),
    date: today.date
  });

  useEffect(() => {
    setCurrentTime({
      hour: today.hour,
      minute: today.minute,
      day: new Date().toLocaleString('default', { weekday: 'short' }),
      month: new Date().toLocaleString('default', { month: 'short' }),
      date: today.date
    });
  }, [today.minute]);

  return (
    <div className="time-display">
      <div className="time">
        {currentTime.hour}:{currentTime.minute < 10 ? '0' + currentTime.minute : currentTime.minute}
      </div>
      <div className="day">
        {currentTime.day}, {currentTime.month}
        <span className="date">{currentTime.date}</span>
      </div>
    </div>
  );
}

export default TimeDisplay;
